// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–ø—Ä–∞–≤–∫–∏ –¥–∞–Ω–∏—Ö –∑ —Ñ–æ—Ä–º–∏
export async function postData(email) {
  return axios.post(`${references.SECOND_URL}`, { email });
}

export async function postOrder(obj) {
  return await axios
  .post("https://food-boutique.b.goit.study/api/orders", obj)
  
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞ –∑ —Ç–µ–∫—Å—Ç–æ–º
function showModal(title, text) {
  const modalBackdrop = document.querySelector('[data-modal]');
  const modalTitle = modalBackdrop.querySelector('.modal-title');
  const modalText = modalBackdrop.querySelector('.modal-text');

  modalTitle.textContent = title;
  modalText.textContent = text;

  modalBackdrop.classList.remove('is-hidden');
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –∑–∞–∫—Ä–∏—Ç—Ç—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞
function closeModal() {
  const modalBackdrop = document.querySelector('[data-modal]');
  modalBackdrop.classList.add('is-hidden');
}

// –û—Ç—Ä–∏–º–∞–Ω–Ω—è —Ñ–æ—Ä–º–∏ —Ç–∞ –¥–æ–¥–∞–≤–∞–Ω–Ω—è –æ–±—Ä–æ–±–Ω–∏–∫–∞ –ø–æ–¥—ñ–π –¥–ª—è –ø–æ–¥—ñ—ó submit
const form = document.getElementById('subscriptionForm');
form.addEventListener('submit', async function (event) {
  event.preventDefault(); // –ó—É–ø–∏–Ω–∏—Ç–∏ –ø–µ—Ä–µ–∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏ –ø—Ä–∏ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—ñ –∫–Ω–æ–ø–∫–∏

  const emailInput = document.getElementById('emailInput');
  const email = emailInput.value; // –û—Ç—Ä–∏–º–∞–Ω–Ω—è –∑–Ω–∞—á–µ–Ω–Ω—è –ø–æ–ª—è email

  postData(email)
    .then(response => {
      const responseData = response.data;
      showModal(
        'Thanks for subscribing for new products',
        'We promise you organic and high-quality products that will meet your expectations. Please stay with us and we promise you many pleasant surprises.'
      );
    })
    .catch(error => {
      const errorMessage = error.response
        ? 'This email address has already been entered'
        : 'An error occurred';
      showModal(
        errorMessage,
        'You have already subscribed to our new products. Watch for offers at the mailing address.'
      );
    });
});

// –î–æ–¥–∞–≤–∞–Ω–Ω—è –æ–±—Ä–æ–±–Ω–∏–∫–∞ –ø–æ–¥—ñ–π –¥–ª—è –∑–∞–∫—Ä–∏—Ç—Ç—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞ –ø—Ä–∏ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—ñ –Ω–∞ –∫–Ω–æ–ø–∫—É –∑–∞–∫—Ä–∏—Ç—Ç—è
const modalCloseBtn = document.querySelector('[data-modal-close]');
modalCloseBtn.addEventListener('click', closeModal);

// –î–æ–¥–∞–≤–∞–Ω–Ω—è –æ–±—Ä–æ–±–Ω–∏–∫–∞ –ø–æ–¥—ñ–π –¥–ª—è –∑–∞–∫—Ä–∏—Ç—Ç—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞ –ø—Ä–∏ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—ñ –∫–ª–∞–≤—ñ—à—ñ "Esc"
document.addEventListener('keydown', function (event) {
  const modalBackdrop = document.querySelector('[data-modal]');
  if (
    event.key === 'Escape' &&
    !modalBackdrop.classList.contains('is-hidden')
  ) {
    closeModal();
  }
});
// –î–æ–¥–∞–≤–∞–Ω–Ω—è –æ–±—Ä–æ–±–Ω–∏–∫–∞ –ø–æ–¥—ñ–π –¥–ª—è –∑–∞–∫—Ä–∏—Ç—Ç—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞ –ø—Ä–∏ –∫–ª—ñ–∫—É –ø–æ–∑–∞ –º–æ–¥–∞–ª—å–Ω–∏–º –≤—ñ–∫–Ω–æ–º
const modalBackdrop = document.querySelector('[data-modal]');
modalBackdrop.addEventListener('click', function (event) {
  if (event.target === modalBackdrop) {
    closeModal();
  }
});
